.Navbar {
    padding-top: 8px;
    text-align: center;
    font-size: 20px;
    color: antiquewhite;
    background-color: rgb(53, 52, 52);
    width: 100%;
    height: 50px;
}

a {
    padding: 15px;
    padding-right: 20px;
    padding-left: 20px;
}

.icon:active {
    background-color: black;
}

.icon:hover {
    background-color: black;
}

.underline {
    text-decoration: underline;
}

.name {
    position: absolute;
    font-weight: bold;
    padding-left: 15px;
    font-size: 20px;
}

.status {
    position: absolute;
    right: 10px;
    top: 10px;
    font-size: 18px;
}

